import { Prefs } from "../../alias/prefs";
export function createSafeOriginUrl(url) {
    const defaultOrigin = Prefs.get("ipm_default_origin");
    const safeOrigins = Prefs.get("ipm_safe_origins");
    let safeOriginUrl;
    try {
        safeOriginUrl = new URL(url, defaultOrigin);
    }
    catch (ex) {
        return null;
    }
    if (!safeOrigins.includes(safeOriginUrl.origin)) {
        return null;
    }
    return safeOriginUrl.href;
}
